class RestaurantAddress extends HTMLElement {
  connectedCallback() {
    this._address = this.getAttribute("address");
    this._city = this.getAttribute("city");
    this.render();
  }

  render() {
    this.innerHTML = `
    <div class="icon-par-detail">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="15"
          height="15"
          fill="currentColor"
          class="bi bi-geo-alt-fill"
          viewBox="0 0 16 16"
        >
          <path
            d="M8 16s6-5.686 6-10A6 6 0 0 0 2 6c0 4.314 6 10 6 10m0-7a3 3 0 1 1 0-6 3 3 0 0 1 0 6"
          />
        </svg>
        <p class="restaurant-address">
          Address : ${this._address} - ${this._city}
        </p>
        </div>
        `;
  }
}
customElements.define("restaurant-address", RestaurantAddress);
