import API_ENDPOINT from "../../globals/api-endpoint";

class RestaurantCard extends HTMLElement {
  constructor() {
    super();
    this["_picture-id"] = this.getAttribute("picture-id");
    this._name = this.getAttribute("name");
    this._description = this.getAttribute("description");
    this._rating = this.getAttribute("rating");
    this._city = this.getAttribute("city");
    this._id = this.getAttribute("id");
  }

  static get observedAttributes() {
    return ["id", "picture-id", "name", "description", "rating", "city"];
  }

  connectedCallback() {
    this.render();
  }

  attributeChangedCallback(name, _, newValue) {
    this[`_${name}`] = newValue;
  }

  render() {
    this.innerHTML = `
      <article class="card">
        <img
          src="${API_ENDPOINT.IMAGE("medium", this["_picture-id"])}"
          alt="Restoran ${this._name}"
        />

        <p class="restaurant-name">${this._name}</p>
        <p class="restaurant-desc">${this._description.slice(0, 100)}...</p>
        <div class="card-bottom">
          <div class="icon-par">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="15"
              height="15"
              fill="currentColor"
              class="bi bi-star-fill"
              viewBox="0 0 16 16"
            >
              <path
                d="M3.612 15.443c-.386.198-.824-.149-.746-.592l.83-4.73L.173 6.765c-.329-.314-.158-.888.283-.95l4.898-.696L7.538.792c.197-.39.73-.39.927 0l2.184 4.327 4.898.696c.441.062.612.636.282.95l-3.522 3.356.83 4.73c.078.443-.36.79-.746.592L8 13.187l-4.389 2.256z"
              />
            </svg>
            <p class="restaurant-rate">Rating : ${this._rating}</p>
          </div>
          <div class="icon-par">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="15"
              height="15"
              fill="currentColor"
              class="bi bi-geo-alt-fill"
              viewBox="0 0 16 16"
            >
              <path
                d="M8 16s6-5.686 6-10A6 6 0 0 0 2 6c0 4.314 6 10 6 10m0-7a3 3 0 1 1 0-6 3 3 0 0 1 0 6"
              />
            </svg>
            <p class="restaurant-city">Lokasi : ${this._city}</p>
          </div>
          <div class="button-container">
          <a class="detail-button" href="#/detail/${this._id}"> Detail </a></div>
        </div>
      </article>
    `;
  }
}

customElements.define("restaurant-card", RestaurantCard);
